/* buttons */
.btn {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 10.5px 10px;
  margin-bottom: 0;
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  vertical-align: top;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
  border: 1px solid currentColor;
  background: transparent;
  border-radius: 7px;
  text-decoration: none;
  position: relative;
  min-width: auto;
  background-position: center;
  vertical-align: middle;
  line-height: 1;
  overflow: hidden;
  transition: background 0.5s;
  background-size: 0%; }
  .btn > span, .btn > i, .btn > svg {
    position: relative;
    z-index: 1; }
  .btn .icon {
    margin: -5px 10px;
    width: 24px;
    height: 24px; }
  .btn .count {
    display: block;
    line-height: 20px;
    min-width: 20px;
    padding: 0 5px;
    color: var(--tg-theme-bg-color);
    font-size: 13px;
    background: var(--color-d-3);
    border-radius: 50%;
    margin: -5px 10px; }
  .btn *:first-child {
    margin-left: 0 !important; }
  .btn *:last-child {
    margin-right: 0 !important; }
  .btn.--xl {
    padding-top: 20.5px;
    padding-bottom: 20.5px; }
    .btn.--xl .icon {
      margin-top: -12.5px;
      margin-bottom: -12.5px;
      width: 28px;
      height: 28px; }
  .btn.--lg {
    padding-top: 17.5px;
    padding-bottom: 17.5px; }
    .btn.--lg .icon {
      margin-top: -12.5px;
      margin-bottom: -12.5px; }
  .btn.--sm {
    padding-top: 9px;
    padding-bottom: 9px; }
    .btn.--sm .icon {
      margin-top: -2px;
      margin-bottom: -2px;
      width: 18px;
      height: 18px; }
  .btn.--xs {
    padding-top: 6.5px;
    padding-bottom: 6.5px;
    padding-left: 10px;
    padding-right: 10px; }
    .btn.--xs .icon {
      margin-top: -0.5px;
      margin-bottom: -0.5px;
      width: 14px;
      height: 14px; }
  .btn.--primary {
    background-color: var(--color-d-6);
    border-color: var(--color-d-6);
    color: var(--tg-theme-bg-color); }
    .btn.--primary .icon {
      fill: var(--tg-theme-bg-color); }
  .btn.--secondary {
    background-color: var(--color-d-1);
    border-color: var(--color-d-1);
    color: var(--tg-theme-bg-color); }
    .btn.--secondary .icon {
      fill: var(--tg-theme-bg-color); }
  .btn.--secondary-alt {
    background-color: var(--color-l-4);
    border-color: var(--color-l-4);
    color: var(--color-d-1); }
    .btn.--secondary-alt .icon {
      fill: var(--color-d-1); }
  .btn.--accent {
    background-color: var(--color-d-3);
    border-color: var(--color-d-3);
    color: var(--tg-theme-bg-color); }
    .btn.--accent .icon {
      fill: var(--tg-theme-bg-color); }
  .btn.--contrast {
    background-color: var(--color-d-5);
    border-color: var(--color-d-5);
    color: var(--tg-theme-bg-color); }
    .btn.--contrast .icon {
      fill: var(--tg-theme-bg-color); }
  .btn.--outline {
    background-color: transparent;
    color: currentColor; }
    .btn.--outline.--secondary {
      color: var(--color-d-1); }
    .btn.--outline.--secondary-alt {
      color: var(--color-d-1); }
    .btn.--outline.--accent {
      color: var(--color-d-3); }
    .btn.--outline.--contrast {
      color: var(--color-d-5); }
  .btn.--ghost {
    background-color: var(--tg-theme-bg-color);
    border-color: var(--tg-theme-bg-color);
    color: currentColor; }
    .btn.--ghost.--primary .icon {
      fill: var(--color-d-6); }
    .btn.--ghost.--secondary {
      color: var(--color-d-1); }
      .btn.--ghost.--secondary .icon {
        fill: var(--color-d-1); }
    .btn.--ghost.--secondary-alt {
      color: var(--color-d-1); }
      .btn.--ghost.--secondary-alt .icon {
        fill: var(--color-d-1); }
    .btn.--ghost.--accent {
      color: var(--color-d-3); }
      .btn.--ghost.--accent .icon {
        fill: var(--color-d-3); }
    .btn.--ghost.--contrast {
      color: var(--color-d-5); }
      .btn.--ghost.--contrast .icon {
        fill: var(--color-d-5); }
  .btn.--link {
    padding-left: 0;
    padding-right: 0;
    border-color: transparent;
    color: var(--color-d-1); }
    .btn.--link > span {
      border-bottom: 1px solid currentColor;
      line-height: 21px;
      margin: -6px 0; }
    .btn.--link.--xl > span {
      line-height: 24px; }
    .btn.--link.--xs > span {
      line-height: 18px; }
    .btn.--link .icon {
      fill: var(--color-d-1); }
  .btn.--r0 {
